<template>
  <div id="app">
    <Board ref="board"/>
  </div>
</template>

<script>
import Board from './components/Board'
import loadImage from 'blueimp-load-image'

export default {
  name: 'app',
  components: {
    Board
  },

  mounted () {
    this.autostart()
  },

  computed: {
    viewportHeight () {
      return Math.max(document.documentElement.clientWidth, window.innerWidth || 0)
    },

    viewportWidth () {
      return Math.max(document.documentElement.clientHeight, window.innerHeight || 0)
    }
  },

  methods: {
    autostart () {
      loadImage('static/fingers.jpg', canvas => {
        this.start({
          image: canvas.toDataURL(),
          size: { horizontal: 3, vertical: 3 }
        })
      }, {
        minWidth: this.viewportWidth,
        maxWidth: this.viewportWidth,
        minHeight: this.viewportHeight,
        maxHeight: this.viewportHeight,
        canvas: true
      })
    },

    start (...args) {
      this.$refs.board.start(...args)
    }
  }
}
</script>

<style lang="scss">
*, *::before, *::after {
  box-sizing: border-box;
}
</style>



